		loadmore(index){
			if(this.newslist[index].loadtext!="上拉加载更多"){ return; }
			// 修改状态
			this.newslist[index].loadtext="加载中...";
			// 获取数据
			setTimeout(()=> {
				//获取完成
				let obj={
					userpic:"../../static/demo/userpic/12.jpg",
					username:"昵称",
					isguanzhu:false,
					title:"我是标题",
					type:"img", // img:图文,video:视频
