						dingnum:11,
						cainum:11,
					},
					commentnum:10,
					sharenum:10,
				};
				this.newslist[index].list.push(obj);
				this.newslist[index].loadtext="上拉加载更多";
			}, 1000);
			// this.newslist[index].loadtext="没有更多数据了";
		},
		// tabbar点击事件
		tabtap(index){
			this.tabIndex=index;
		},
		// 滑动事件
		tabChange(e){
			this.tabIndex=e.detail.current;
		}
